.cookie-notification {
  background-color: var(--color-grey);
  border-radius: var(--block-border-radius);
  justify-content: center;
  align-items: center;
  gap: 50px;
  max-width: 70%;
  height: 60px;
  margin: 0 auto;
  display: flex;
  position: fixed;
  bottom: 10px;
  left: 0;
  right: 0;
}

.notification_visible {
  opacity: 1;
  visibility: visible;
  transition: opacity .1s linear, visibility .1s linear;
}

.notification_hidden {
  opacity: 0;
  visibility: hidden;
  transition: opacity .1s linear, visibility .1s linear;
}

.cookie-notification__content {
  font-size: calc(var(--base-font-size) * 1.1);
  margin: 0;
}

.cookie-notification__button {
  font-size: calc(var(--base-font-size) * .9);
}

@media (width <= 1024px) {
  .cookie-notification {
    flex-direction: column;
    gap: 15px;
    height: auto;
    padding: 20px;
  }

  .cookie-notification__content {
    text-align: center;
    font-size: 20px;
  }

  .cookie-notification__button {
    font-size: calc(var(--base-font-size) * .8);
  }
}

@media (prefers-color-scheme: dark) {
  .cookie-notification {
    box-shadow: 0 0 5px 0 var(--color-black);
    background-color: var(--color-dark-scheme-grey);
  }
}

* {
  box-sizing: border-box;
}

:root {
  font-family: Noto Sans, sans-serif;
  font-size: var(--base-font-size);
  scroll-behavior: smooth;
  --base-font-size: 20px;
  --color-grey: #ebebeb;
  --color-black: #000;
  --color-light-black: #414141;
  --color-white: #fff;
  --block-border-radius: 10px;
  --button-border-radius: 6px;
}

body {
  margin: 0;
}

main {
  margin: 100px 0 250px;
}

a {
  color: var(--color-black);
  text-decoration-color: #0000;
  transition: text-decoration-color .125s linear;
}

a:hover {
  text-decoration-color: inherit;
}

h1 {
  font-size: calc(var(--base-font-size) * 1.2);
  text-align: center;
  padding-top: 10px;
}

h2 {
  font-size: calc(var(--base-font-size) * 1.1);
  text-align: center;
  margin: 0;
  padding: 15px;
  font-weight: 400;
}

p {
  text-align: justify;
}

ul {
  list-style: none;
}

.button {
  font-size: calc(var(--base-font-size) * .8);
  border-radius: var(--button-border-radius);
  color: var(--color-white);
  background-color: var(--color-black);
  user-select: none;
  border: none;
  padding: 10px 25px;
  transition: background-color .1s linear;
}

.button:hover {
  cursor: pointer;
  background-color: var(--color-light-black);
}

.nav-link {
  user-select: none;
}

@media (width <= 1024px) {
  main {
    margin: 20px 0 150px;
  }

  h1 {
    font-size: calc(var(--base-font-size) * 1.1);
  }

  h2 {
    font-size: var(--base-font-size);
  }
}

@media (prefers-color-scheme: dark) {
  :root {
    color-scheme: dark;
    --color-dark-scheme-grey: #3b3e44;
    --color-dark-scheme-light-grey: #484b51;
  }

  a {
    color: var(--color-white);
  }

  svg {
    fill: var(--color-dark-scheme-grey);
    transition: fill .25s;
  }

  svg:hover {
    fill: var(--color-dark-scheme-light-grey);
  }

  li:hover {
    text-shadow: 0 0 var(--color-white);
  }

  .button:hover {
    background-color: #212121;
  }

  canvas {
    border-radius: var(--block-border-radius);
    background-color: #3b3e441a;
  }
}

header {
  justify-content: end;
  margin: 30px 40px 0 0;
  display: flex;
}

.menu__icon {
  cursor: pointer;
  user-select: none;
  transition: transform .1s linear, fill .125s;
}

.menu__icon_active {
  transform: rotate(90deg);
}

.menu__list {
  border-radius: var(--block-border-radius);
  background-color: var(--color-grey);
  margin: 0;
  padding: 30px 40px;
  transition: opacity .1s linear, visibility .1s linear;
  display: block;
  position: absolute;
  top: 10px;
  right: 100px;
}

.menu__list li {
  padding-top: 7px;
}

.menu__list_closed {
  opacity: 0;
  visibility: hidden;
}

.menu__list_opened {
  opacity: 1;
  visibility: visible;
}

@media (width <= 1024px) {
  .menu__list {
    right: 85px;
  }

  .menu__list li {
    font-size: 20px;
  }
}

@media (prefers-color-scheme: dark) {
  .menu__list {
    background-color: var(--color-dark-scheme-grey);
  }
}

.intro {
  grid-template-columns: 1fr 1fr;
  place-items: center;
  gap: 50px 10px;
  display: grid;
}

.intro__picture {
  z-index: -1;
  animation: 250s infinite endless-rotation;
}

@keyframes endless-rotation {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.intro__content-wrapper {
  max-width: 70%;
}

.intro__title {
  margin: 0;
  padding: 0;
  font-weight: 600;
}

.intro__subtitle {
  padding: 0 0 15px;
}

.intro__title, .intro__subtitle {
  text-align: start;
}

.intro__button {
  float: right;
}

.intro__arrow-icon {
  grid-area: 2 / 1 / 3 / 3;
  transition: transform .2s ease-in;
}

.intro__arrow-icon:hover {
  cursor: pointer;
  transition: transform .2s ease-in-out;
  transform: translateY(3px);
}

@media (width <= 1024px) {
  .intro {
    grid-template-columns: 1fr;
    place-items: center;
    gap: 60px 0;
  }

  .intro__picture {
    grid-row: 1 / 2;
    animation: none;
  }

  .intro__content-wrapper {
    grid-row: 2 / 3;
    max-width: 80%;
  }

  .intro__arrow-icon {
    grid-row: 3 / 4;
  }
}

@media (prefers-color-scheme: dark) {
  .intro__button {
    background-color: var(--color-dark-scheme-grey);
  }

  .intro__button:hover {
    background-color: var(--color-dark-scheme-light-grey);
  }
}

.forecast {
  margin: 100px 0 150px;
}

.forecast__wrapper {
  flex-direction: column;
  align-items: center;
  gap: 100px;
  max-width: 900px;
  margin: 0 auto;
  display: flex;
}

.forecast__chart-wrapper {
  width: 100%;
}

.forecast__chart-alternative {
  text-align: center;
  background-color: var(--color-grey);
  border-radius: var(--block-border-radius);
  padding: 30px;
}

@media (width <= 1024px) {
  .forecast {
    margin: 100px 0;
  }

  .forecast__wrapper {
    gap: 90px;
    max-width: 700px;
  }
}

.test__title {
  margin-bottom: 60px;
}

.test__wrapper {
  background: linear-gradient(0deg, var(--color-white) 0%, var(--color-grey) 100%);
  border: 1px solid var(--color-grey);
  border-radius: var(--block-border-radius);
  flex-direction: column;
  align-items: center;
  max-width: 1000px;
  height: 150px;
  margin: 0 auto;
  display: flex;
  position: relative;
}

.test__wrapper_opened {
  background: linear-gradient(0deg, var(--color-white) 80%, var(--color-grey) 100%);
  height: auto;
  overflow: visible;
}

.test__wrapper_closed {
  overflow: hidden;
}

.test__subtitle {
  margin-bottom: 70px;
}

.test__text {
  padding: 0 80px;
}

.test__text:before {
  content: "";
  background: url("quiz.42613fda.svg");
  width: 40px;
  height: 40px;
  display: block;
  position: absolute;
  left: 20px;
}

.test__button {
  margin-bottom: 10px;
  position: absolute;
  bottom: 10px;
}

.test__inner {
  outline: 1px solid var(--color-black);
  border-radius: var(--block-border-radius);
  background-color: inherit;
  width: 60%;
  height: 300px;
  box-shadow: 0 0 8px 0px var(--color-black);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 35px;
  margin: 30px auto 80px;
  display: flex;
}

.test__inner_test-finished {
  height: auto;
}

.question {
  margin: 0;
  padding: 30px 10px 0;
}

.test__answers {
  gap: 10px;
  display: flex;
}

.test__answers_test-finished {
  flex-direction: column;
}

.test__answers_test-finished p {
  padding: 0 30px;
}

.test__answers_test-finished p:after {
  content: ";";
}

.test__answers_test-finished p:last-child:after {
  content: ".";
}

.answer-button_hidden {
  visibility: hidden;
  display: none;
}

.progress-done-icon:after {
  content: "";
  vertical-align: middle;
  background: url("done.f0844a54.svg");
  width: 40px;
  height: 40px;
  margin-left: 5px;
  display: inline-block;
}

@media (width <= 1024px) {
  .test__wrapper {
    max-width: 350px;
  }

  .test__text {
    text-align: initial;
    padding: 0 20px 0 60px;
  }

  .test__text:before {
    left: 10px;
  }

  .test__inner {
    width: 95%;
  }

  .question {
    text-align: center;
    height: 80px;
  }

  .answer-button {
    padding: 15px 30px;
  }

  .test__answers_test-finished p {
    text-align: left;
  }
}

@media (prefers-color-scheme: dark) {
  .test__wrapper {
    border-color: var(--color-black);
    background: var(--color-dark-scheme-grey);
  }
}

footer {
  justify-content: end;
  display: flex;
}

.footer-links {
  gap: 60px;
  margin-right: 50px;
  padding: 0;
  display: flex;
}

.footer-links a {
  align-items: center;
  display: inline-flex;
}

.github-link {
  gap: 15px;
}

@media (width <= 1024px) {
  footer {
    justify-content: center;
  }

  .footer-links {
    margin: 10px 0;
  }

  .footer-links li {
    font-size: var(--base-font-size);
  }
}
